<!--商品分类页 -->
<template>
  <div v-loading="loading">
    <cms-breadcrumb to="/productCategory/list" type="list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <el-form
        ref="form"
        :rules="rules"
        :model="formData"
        label-width="210px"
        @submit.native.prevent
      >
        <el-form-item label="上级分类：" prop="nameAlias">
          <el-cascader
            :options="options"
            :props="props"
            :change-on-select="true"
            v-model="formData.parentIds"
          ></el-cascader>
          <span class="gray">不选择，默认为顶级分类，保存后，此项无法修改，请谨慎选择</span>
        </el-form-item>
        <el-form-item label="分类名称：" prop="cateName">
          <el-input @change="getCateName" maxlength="150" v-model="formData.cateName"></el-input>
        </el-form-item>
        <el-form-item label="分类别名：" prop="nameAlias">
          <el-input maxlength="150" v-model="formData.nameAlias"></el-input>
          <span class="gray">设置分类别名，商城显示优先显示别名</span>
        </el-form-item>
        <el-form-item label="保证金：" prop="bondPrice">
          <el-input maxlength="10" v-model="formData.bondPrice"></el-input>
          <span class="gray">设置商家入住时主营类目需要缴纳的保证金</span>
        </el-form-item>
        <el-form-item label="佣金比例：" prop="commissionRate">
          <el-input maxlength="10" v-model="formData.commissionRate">
            <template slot="append">%</template>
          </el-input>
          <span class="gray">设置商家入住时经营分类需要支付给平台的费用比例</span>
        </el-form-item>
        <el-form-item label="是否显示：" prop="show">
          <cms-switch v-model="formData.show"></cms-switch>
          <span class="gray">设置该分类是否显示在商城分类导航中</span>
        </el-form-item>
        <el-form-item label="是否为虚拟分类：" prop="virtual">
          <cms-switch v-model="formData.virtual"></cms-switch>
        </el-form-item>
        <el-form-item label="分类模版：" prop="templatePath">
          <el-select v-model="formData.templatePath">
            <el-option label="默认模板" value="默认模板"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品类型：" prop="productTypeId">
          <el-select v-model="formData.productTypeId" filterable>
            <el-option
              v-for="item in productTypeItems"
              :label="item.name"
              :value="item.id"
              :key="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分类图标：" prop="resourcesSpaceId" class="cates-logo">
          <cms-picture-zone v-model="formData.resourcesSpaceId" v-if="status">
            <cms-button size="small" type="info" icon="correlation">选择图标</cms-button>
          </cms-picture-zone>
          <!-- 选择图标组件 -->
        </el-form-item>
        <el-form-item label="分类页标题：" prop="cateTitle">
          <el-input maxlength="150" v-model="formData.cateTitle"></el-input>
        </el-form-item>
        <el-form-item label="分类页关键字：" prop="cateKewords">
          <el-input maxlength="255" v-model="formData.cateKewords"></el-input>
        </el-form-item>
        <el-form-item label="分类页描述：" prop="cateDescription">
          <el-input maxlength="255" type="textarea" :rows="4" v-model="formData.cateDescription"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button v-prem="'/productCategory/save'" @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button v-prem="'/productCategory/save'" @click="save(true)" v-color>保存</cms-button>
        <cms-button type="rest" @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { productTypeList } from '@/api/product/type'
import {
  productCategorySave,
  productCategoryTree
} from '@/api/product/category.js'
var defaultData = {
  cateName: '',
  nameAlias: '',
  parentId: '',
  parentIds: [],
  bondPrice: 0,
  commissionRate: 10,
  show: true,
  virtual: false,
  templatePath: '默认模板',
  productTypeId: '',
  resourcesSpaceId: '',
  cateTitle: '',
  cateKewords: '',
  cateDescription: ''
}
export default {
  mixins: [form],
  data() {
    return {
      loading: false,
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      roleList: ['超级管理员', '运营'],
      formData: Object.assign({}, defaultData),
      rules: {
        cateName: [rules.required()],
        show: [rules.required()],
        virtual: [rules.required()],
        templatePath: [rules.required()],
        productTypeId: [rules.required('必须选择一个类型', 'change')],
        bondPrice: [rules.required(), rules.decimal()],
        commissionRate: [rules.required(), rules.number()],
        resourcesSpaceId: [rules.required('请选择图标', 'change')]
      },
      productTypeItems: []
    }
  },
  methods: {
    getCateName(val) {
      this.formData.cateTitle = val
      this.formData.cateKewords = val
      this.formData.cateDescription = val
    },
    getMenuTree() {
      // 获取菜单节点
      productCategoryTree().then(res => {
        const root = [{ id: 0, cateName: '根目录' }]
        this.options = root.concat(res.data)
      })
    },
    getProductTypeItems() {
      productTypeList().then(res => {
        this.productTypeItems = res.data
      })
    },
    save(state) {
      const params = this.$cms.deepCopy(this.formData)
      params.parentId = params.parentIds[params.parentIds.length - 1]
      delete params.parentIds // 处理掉参数
      this.saveForm(
        productCategorySave,
        params,
        state,
        '/productCategory/list',
        'list'
      ).then(res => {
        this.getMenuTree()
      })
    },
    rest() {
      window.clearInterval(this.timer)
      this.autoFocus()
      this.$refs.form.resetFields()
      this.destroyed()
      this.formData.parentIds = [0]
    },
    getAll() {
      this.getMenuTree()
      this.getProductTypeItems()
    }
  },
  created() {
    this.formData = Object.assign({}, defaultData)
    this.formData.parentIds = this.$cms.arrayToNumber(
      this.$route.query.nodeIds
    )
    this.formData.commissionRate = this.$route.query.rate
    this.getAll()
  }
}
</script>





